.mobile {
  min-height: 864px;
  padding: 48px 0 80px 0;
  background: url('../img/mobile.png') center center/cover no-repeat;
  .row {
    margin-top: 32px; }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
      background: url('../img/pic1.png') center center/cover no-repeat; }
    &_2 {
      background: url('../img/pic2.png') center center/cover no-repeat; }
    &_3 {
      background: url('../img/pic3.png') center center/cover no-repeat; }
    &_4 {
      background: url('../img/pic4.png') center center/cover no-repeat; }
    &_5 {
      background: url('../img/pic5.png') center center/cover no-repeat; }
    &_6 {
      background: url('../img/pic6.png') center center/cover no-repeat; }
    &_subtitle {
      font-size: 24px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.29;
      letter-spacing: normal;
      text-align: center;
      color: #fff; }
    &_plus {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      bottom: 35px;
      left: 50%;
      transform: translate(-50%);
      width: 26px;
      height: 26px;
      background-color: #000;
      margin: 0 auto;
      border-radius: 100%;
      padding: 6px;
      cursor: pointer;
      &::before {
        content: '';
        display: block;
        width: 14px;
        height: 2px;
        background-color: $main-color; }
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translate(-50%);
        display: block;
        height: 14px;
        width: 2px;
        background-color: $main-color;
        transition: 0.3s all; }
      &:hover {
        &:before, &:after {
          background-color: $second-color; } } } } }

